import React from 'react';
import { API_URL } from '../api/apiURL';

const ReportModal = ({ report, onClose, refreshReports }) => {
  if (!report) return null;

  const {
    reporter,
    reportedUser,
    title,
    description,
    image,
    status,
    createdAt,
  } = report;

  const formatDate = (date) => new Date(date).toLocaleDateString();

  const handleBackdropClick = (e) => {
    if (e.target.id === "backdrop") {
      onClose();
    }
  };

  // FINAL NEW LOGIC
  const handleAction = async (action) => {
    try {
      let payload;

      if (action === "approve") {
        // ‚úî APPROVE ‚Üí BLOCK USER + REPORT APPROVED
        payload = {
          status: "approved",
          blockUser: true
        };
      } else {
        // ‚úî REJECT ‚Üí REPORT APPROVED BUT USER NOT BLOCK
        payload = {
          status: "approved",
          blockUser: false
        };
      }

      const res = await fetch(`${API_URL}/admin/report/status/${report._id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = await res.json();
      console.log("FINAL API RESPONSE:", data);

      if (data.success) {
        if (action === "approve") {
          alert("Report approved & USER BLOCKED");
        } else {
          alert("Report resolved (Approved) but USER NOT BLOCKED");
        }

        onClose();
        refreshReports();
      } else {
        alert(data.message || "Something went wrong");
      }
    } catch (err) {
      console.error(err);
      alert("Error processing action");
    }
  };

  return (
    <div
      id="backdrop"
      onClick={handleBackdropClick}
      className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 backdrop-blur-sm px-4 py-10"
    >
      <div
        className="relative bg-white/90 backdrop-blur-lg border border-white/40 rounded-3xl shadow-2xl max-w-xl w-full p-8 animate-scaleIn"
        onClick={(e) => e.stopPropagation()}
      >

        {/* Status + Date */}
        <div className="flex justify-between items-center mb-6">
          <div className="flex items-center gap-2">
            <div
              className={`w-3 h-3 rounded-full ${
                status === "approved"
                  ? "bg-green-500"
                  : status === "rejected"
                  ? "bg-red-500"
                  : "bg-yellow-400"
              }`}
            />
            <span className="text-sm font-medium capitalize text-gray-800">
              {status}
            </span>
          </div>
          <span className="text-sm text-gray-600">
            Reported on: {formatDate(createdAt)}
          </span>
        </div>

        {/* Reporter + Reported */}
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-6 mb-6">
          {[reportedUser, reporter].map((user, idx) => (
            <div
              key={idx}
              className="bg-white rounded-xl border border-gray-200 p-5 flex flex-col items-center text-center hover:shadow-md transition"
            >
              <img
                src={
                  user?.avatar ||
                  user?.profileImage ||
                  "/default-profile.png"
                }
                alt="User"
                className="w-20 h-20 rounded-full border object-cover shadow"
              />
              <h3 className="font-semibold text-lg text-gray-800 mt-3">
                {user?.name || user?.fullName || "Unnamed User"}
              </h3>
              <p className="text-xs text-blue-500 mt-1">
                {idx === 1 ? "Reporter" : "Reported User"}
              </p>
            </div>
          ))}
        </div>

        {/* Reason & Description */}
        <div className="mb-4">
          <h4 className="font-semibold">Reason:</h4>
          <p className="text-sm bg-gray-100 border rounded-lg p-4 mt-1">
            {title}
          </p>

          <h4 className="font-semibold mt-4">Description:</h4>
          <p className="text-sm bg-gray-100 border rounded-lg p-4 mt-1">
            {description}
          </p>
        </div>

        {/* Images */}
        {image?.length > 0 && (
          <div className="mb-4">
            <h4 className="text-sm font-semibold mb-2">Attached Screenshots:</h4>
            <div className="flex flex-wrap gap-2">
              {image.map((img, idx) => (
                <a
                  key={idx}
                  href={img}
                  target="_blank"
                  className="text-sm text-blue-600 bg-blue-100 px-3 py-1 rounded-md"
                >
                  üìé Screenshot_{idx + 1}.jpg
                </a>
              ))}
            </div>
          </div>
        )}

        {/* Approve / Reject */}
        <div className="flex justify-end gap-4 mt-8">
          <button
            onClick={() => handleAction("approve")}
            className="bg-green-500 hover:bg-green-600 text-white px-5 py-2 rounded-full"
          >
            Approve (BLOCK)
          </button>

          <button
            onClick={() => handleAction("reject")}
            className="bg-red-500 hover:bg-red-600 text-white px-5 py-2 rounded-full"
          >
            Reject (NO BLOCK)
          </button>
        </div>
      </div>
    </div>
  );
};

export default ReportModal;
